h2{
  font-weight: bold;
}
h1{
  font-size: 95px;
}
p{
  margin: 1rem;
  text-align: center;
  font-size: 24px;
}
button{
  cursor: pointer;
}
.App{
  max-width: 1500px;
  margin: 0 auto;
}
.Dark-theme{
  color: orange;
  height: 100vh;
  background-color: black;
  transition: 0.5s;
}
.Light-theme{
  transition: 0.5s;
  height: 100vh;
}
.status-tab{
  padding: 5rem 0;
  display: flex;
  justify-content: space-between;
}
.click-bar-light{
  border-radius: 20px;
  margin: 0 1rem;
  width: 5rem;
  height: 2rem;
  background-color: grey;
  transition: 0.2s ;
  border:none;
}
.click-bar-black{
  border-radius: 20px;
  margin: 0 1rem;
  width: 5rem;
  height: 2rem;
  background-color: orange;
  transition: 0.2s ;
  border:none;
}
.click-box-light{
  position: relative;
  top: -0.5px;
  right: -2px;
  height: 1.6rem;
  width: 1.6rem;
  border-radius: 100%;
  background-color: white;
  transition: 0.1s ; 
}
.click-box-dark{
  position: relative;
  top: -0.5px;
  right: -50px;
  height: 1.6rem;
  width: 1.6rem;
  border-radius: 100%;
  background-color: white;
  transition: 0.1s ; 
}
.main-detail{
  display: flex;
}
.detail-text-dark{
  justify-content: center;
  align-items: center;
}
